@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #171727;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 13px;
  color: #fff;
  flex-wrap: wrap;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  display: flex;
  margin-bottom: 32px;
  border-radius: 20px;
  text-align: center;
  justify-content: center;
  color: #4801ff;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 20px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #e43259 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
}
.coin-input:focus {
  outline: none;
}

.coin-input:-ms-input-placeholder {
  color: #1f0b0b;
}

.coin-input::placeholder {
  color: #1f0b0b;
}

@media only screen and (max-width: 768px) {
  .coin-text {
    font-size: 30px;
    font-weight: 200;
    margin-bottom: 32px;
    justify-content: center;
    text-align: center;
    color: #4801ff;
  }
}

@media only screen and (max-width: 450px) {
}

.coin-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.coin-row{
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}

.coin{
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}

.coin h1{
    font-size: 16px;
    width: 150px;
}

.coin img{
    height: 30px;
    width: 30;
    margin-right: 15px;
}

.coin-symbol{
    text-transform: capitalize;
}

.coin-data{
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    width: 110px;
}

.coin-volume{
    width: 200px;
}

.coin-marketcap{
    width: 240px;
}

.coin-percent{
    width: 80px;
}

.red{
    color: #f00606;
}

.green{
    color: #11d811;
}
