@import url("https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@400;600&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: #171727;
  color: #fff;
}

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 13px;
  color: #fff;
  flex-wrap: wrap;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  display: flex;
  margin-bottom: 32px;
  border-radius: 20px;
  text-align: center;
  justify-content: center;
  color: #4801ff;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 20px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #e43259 0%,
    #7918f2 48%,
    #4801ff 100%
  );
  color: #e2e2e2;
}
.coin-input:focus {
  outline: none;
}

.coin-input::placeholder {
  color: #1f0b0b;
}

@media only screen and (max-width: 768px) {
  .coin-text {
    font-size: 30px;
    font-weight: 200;
    margin-bottom: 32px;
    justify-content: center;
    text-align: center;
    color: #4801ff;
  }
}

@media only screen and (max-width: 450px) {
}
